AiB;EACjB,oBAAoB;EACpB,qBAAqB;AACvB;;AAEA,gCAAgC;AAChC;EACE,iBAAiB;EACjB,eAAe;EACf,eAAe;EACf,kBAAkB;EAClB,eAAe;EACf,+BAA+B;AACjC;AACA,oCAAoC;AACpC;EACE,wBAAwB;AAC1B;;AAEA,oCAAoC;AACpC,qCAAqC;AACrC;EACE,aAAa;EACb,UAAU;EACV,gBAAgB;EAChB,uBAAuB;EACvB,iBAAiB;EACjB,yBAAyB;EACzB,aAAa;AACf;AACA,sCAAsC;AACtC;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,aAAa;EACb,qBAAqB;EACrB,qCAAqC;EACrC,0BAA0B;EAC1B,0CAA0C;EAC1C,iCAAiC;EACjC,2CAA2C;EAC3C,gBAAgB;EAChB,UAAU;EACV,qCAAqC;EACrC,yBAAyB;EACzB,gCAAgC;EAChC,sBAAsB;AACxB;AACA;EACE,aAAa;EACb,uBAAuB;EACvB,YAAY;EACZ,kBAAkB;EAClB,wEAAwE;EACxE,mBAAmB;AACrB;AACA;EACE,0BAA0B;EAC1B,kBAAkB;AACpB;AACA;EACE,gBAAgB;EAChB,qBAAqB;EACrB,0BAA0B;AAC5B;AACA;EACE,4BAA4B;EAC5B,aAAa;AACf;AACA;EACE;IACE,UAAU;IACV,2BAA2B;EAC7B;EACA;IACE,UAAU;IACV,wBAAwB;EAC1B;AACF;;AAEA,uCAAuC;AACvC;EACE,kBAAkB;EAClB,gBAAgB;EAChB,cAAc;EACd,eAAe;AACjB;AACA;EACE,YAAY;EACZ,WAAW;AACb;AACA;EACE,6BAA6B;EAC7B,YAAY;AACd;AACA;EACE,YAAY;AACd;;AAEA,4CAA4C;AAC5C;EACE,aAAa;EACb,uBAAuB;EACvB,yBAAyB;EACzB,cAAc;EACd,eAAe;EACf,mBAAmB;EACnB,eAAe;EACf,+BAA+B;EAC/B,gBAAgB;EAChB,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,eAAe;EACf,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;AAClB;AACA;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA,0BAA0B;AAC1B;EACE,eAAe;EACf,QAAQ;EACR,SAAS;EACT,2BAA2B;EAC3B,kCAAkC;EAClC,0BAA0B;EAC1B,yBAAyB;EACzB,mBAAmB;EACnB,cAAc;EACd,aAAa;EACb,gBAAgB;EAChB,oCAAoC;EACpC,aAAa;EACb,2BAA2B;EAC3B,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,cAAc;AAChB;AACA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA,4BAA4B;AAC5B;EACE;IACE,iBAAiB;EACnB;AACF","sourcesContent":["* {\n  box-sizing: border-box;\n  margin: 0;\n}\n\nbody {\n  margin: 0;\n  font-family: 'Inter', sans-serif;\n  /* deep space blue-violet gradient base */\n  background: #0f0c29;\n  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);\n  color: #f3f4f6;\n  /* ensure the page is at least the full viewport */\n  min-height: 100vh;\n  /* allow scrolling */\n  overflow-y: auto;\n}\n\n/* === navigation === */\nheader {\n  background: #1e1b34;\n  padding: 1rem;\n  text-align: center;\n  border-bottom: 1px solid #3a3a52;\n}\nnav ul {\n  list-style: none;\n  display: flex;\n  justify-content: center;\n  gap: 2rem;\n  padding: 0;\n  margin: 0;\n}\nnav li {\n  cursor: pointer;\n  color: #a78bfa;\n  font-weight: 600;\n  transition: color 0.3s ease;\n}\nnav li:hover {\n  color: #f472b6;\n}\nnav li.selected {\n  color: #f472b6;\n  border-bottom: 2px solid #f472b6;\n}\n\n/* === main container === */\n.mainContainer {\n  padding: 2rem;\n  max-width: 1200px;\n  margin: auto;\n}\n\n/* === project grid === */\n.projectGrid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, 200px);\n  justify-content: center;\n  gap: 10px;\n}\n\n#addProjectDiv {\n  display: grid;\n  justify-content: center;\n  padding: 25px;\n}\n\n/* center dialog boxes on window, replaces former #addProjectDialog, #addTaskDialog, and #editTaskDialog */\ndialog {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  margin: 0;\n  padding: 2rem;\n  max-width: 500px;\n  color: #f3f4f6;\n  border-radius: 12px;\n  background: rgba(30, 27, 52, 0.85);\n  backdrop-filter: blur(10px);\n  border: 1px solid rgba(167, 139, 250, 0.2);\n  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);\n  animation: fadeIn 0.3s ease;\n  z-index: 1000;\n  max-height: 90vh;\n  overflow-y: auto;\n  pointer-events: auto;\n}\n\n/* === project card === */\n.projectCard {\n  background: #1e1b34;\n  border: 1px solid #3a3a52;\n  border-radius: 12px;\n  padding: 1rem;\n  transition: transform 0.2s ease, box-shadow 0.2s ease;\n  box-shadow: 0 8px 16px rgba(0,0,0,0.2);\n  cursor: pointer;\n}\n.projectCard:hover {\n  transform: translateY(-3px);\n  box-shadow: 0 12px 24px rgba(0,0,0,0.3);\n}\n.projectCard h3 {\n  font-size: 1.2rem;\n  color: #a78bfa;\n}\n.projectCard p {\n  font-size: 0.90rem;\n  padding: 3px;\n}\n.projectCardBody strong {\n  color: #f472b6;\n}\n/* ensure text inside the card is left-aligned and \ndoes not stretch beyond its container */\n.projectCard h3,\n.projectCard p {\n  margin: 0;\n  text-align: left;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  width: 100%;\n}\n/* priority badge */\n.priorityTag {\n  display: inline-block;\n  padding: 0.3rem 0.7rem;\n  border-radius: 20px;\n  font-size: 0.85rem;\n  font-weight: bold;\n  color: white;\n}\n.priority-low { background: #3b82f6; }\n.priority-medium { background: #facc15; color: #1e1b34; }\n.priority-high { background: #f97316; }\n.priority-urgent { background: #ef4444; }\n\n/* === buttons === */\n#openAddProject {\n  background: #a78bfa;\n  color: #fff;\n  border: none;\n  padding: 0.6rem 1.2rem;\n  border-radius: 6px;\n  font-size: 1.1rem;\n  cursor: pointer;\n  transition: background 0.3s ease;\n}\n#openAddProject:hover {\n  background: #c084fc;\n}\n\n/* === delete button on cards === */\n#deleteProject {\n  display: flex;\n  background: transparent;\n  border: 1px solid #f87171;\n  color: #f87171;\n  font-size: 1rem;\n  font-weight: bolder;\n  cursor: pointer;\n  transition: transform 0.2s ease;\n  padding-top: 2px;\n  border-radius: 25%;\n  width: 24px;\n  height: 24px;\n}\n#deleteProject:hover {\n  transform: scale(1.1);\n  color: #ef4444;\n}\n\n/* header section (title + delete button) */\n.projectCardHeader {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  /* make sure header stays fixed on card expansion */\n  padding-bottom: 10px;\n  top: 0;\n  z-index: 5;\n}\n\n/* prevent overlap of title with delete button */\n.projectCardHeader h3 {\n  max-width: 80%;\n}\n\n/* === modal form layout === */\ndialog form {\n  display: flex;\n  flex-direction: column;\n}\ndialog label {\n  font-weight: 600;\n  font-size: 0.95rem;\n  color: #a78bfa;\n}\ndialog input, dialog textarea, dialog select {\n  padding: 0.6rem 1rem;\n  background: #2a2645;\n  border: 1px solid #3a3a52;\n  border-radius: 8px;\n  color: #f3f4f6;\n  font-size: 1rem;\n  transition: border-color 0.3s;\n}\ndialog input:focus, dialog textarea:focus, dialog select:focus {\n  border-color: #c084fc;\n  outline: none;\n}\n\n/* project modal spacing */\n#addProjectForm input, select {\n  margin-bottom: 10px;\n}\n/* add task and edit task modal spacing */\n#addTaskForm input, #addTaskForm select, #addTaskForm textarea,\n#editTaskForm input, #editTaskForm select, #editTaskForm textarea {\n  margin-bottom: 10px;\n}\n\n/* === modal buttons === */\ndialog .buttonDiv {\n  display: flex;\n  justify-content: center;\n  gap: 1rem;\n  margin-top: 2rem;\n}\ndialog button {\n  padding: 0.5rem 1rem;\n  border-radius: 6px;\n  border: none;\n  cursor: pointer;\n  font-weight: 600;\n  font-size: 1rem;\n}\n#closeAddProject, #closeAddTask, #closeEditTask {\n  background: #2d2d42;\n  color: #f3f4f6;\n}\n#closeAddProject:hover, #closeAddTask:hover, #closeEditTask:hover {\n  background: #26264d;\n}\n#submitAddProject, #submitAddTask, #submitEditTask {\n  background: #a78bfa;\n  color: #fff;\n}\n#submitAddProject:hover, #submitAddTask:hover, #submitEditTask:hover {\n  background: #c084fc;\n}\n\n/* === modal-like background effect === */\n.modalOpen::before {\n  content: \"\";\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background: rgba(0, 0, 0, 0.5);\n  z-index: 5;\n}\n.modalOpen {\n  /* disable clicks on everything, inside and outside the card (reenabled specifically in .expanded below) */\n  pointer-events: none;\n}\n.modalOpen #editTaskDialog {\n  pointer-events: auto;\n  /* above modal overlay and project card */\n  z-index: 15;\n}\n\n.projectCard.expanded {\n  display: flex;\n  flex-direction: column;\n  position: absolute;\n  /* buffer card start from viewport top edge */\n  top: 10vh;\n  left: 50%;\n  /* centers the card horizontally(-50%) but prevents vertical centering(0%) so it grows downward with the content */\n  transform: translate(-50%, 0%);\n  width: 90vw;\n  max-width: 700px;\n  z-index: 10;\n  background: rgba(255, 255, 255, 0.97);\n  color: #1e1b34;\n  padding: 2rem;\n  border-radius: 